*,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
  margin: 0;
  padding: 0; }

a,
a:hover {
  text-decoration: none;
  color: white; }

ul {
  list-style: none; }

body {
  font-family: 'Mulish', sans-serif;
  /* font-family: 'Trirong', serif; */ }

.fixed-top {
  position: fixed !important;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #2D2F5C !important;
  z-index: 9999; }

.toggle {
  width: 40px;
  height: 40px;
  background: transparent;
  display: none;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  cursor: pointer;
  position: relative;
  transition: all 0.5s;
  z-index: 10; }

.toggle-line {
  width: 40px;
  height: 3px;
  background-color: #fff;
  transition: all 0.5s; }

.toggle.active {
  position: relative; }

.toggle.active .toggle-line1 {
  position: absolute;
  transform: rotate(45deg); }

.toggle.active .toggle-line2 {
  position: absolute;
  display: none; }

.toggle.active .toggle-line3 {
  position: absolute;
  transform: rotate(-45deg); }

.header {
  background: url("../images/header__bg.jpg") no-repeat center center/cover;
  padding: 0 0 250px;
  position: relative; }

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0; }

.nav__items {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 25px 30px; }

.logo {
  display: flex;
  align-items: baseline; }

.logo a {
  display: flex;
  align-items: center; }

.logo img {
  margin-right: 9px; }

.logo h2 {
  font-size: 20px;
  text-transform: uppercase;
  color: #fff; }

.nav__bar {
  width: 450px; }

.nav__bar ul.active {
  display: block;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 25px;
  background-color: #2D2F5C;
  z-index: 9; }

.nav__bar ul.active li {
  margin: 15px 0; }

.nav__bar ul {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.nav__link {
  font-size: 14px;
  color: #fff;
  text-transform: capitalize;
  font-weight: 600;
  transition: .6s;
  padding-bottom: 8px; }

.nav__link.active {
  border-bottom: 2px solid #ffcc00;
  color: #ffcc00; }

.header__title {
  padding-top: 240px;
  margin-bottom: 30px; }

.header__title h1 {
  font-family: Trirong;
  font-size: 60px;
  color: #fff;
  line-height: 72px; }

.header__subtitle {
  font-size: 20px;
  color: #fff;
  margin-bottom: 49px; }

.header__button {
  padding: 15px 48px;
  background-color: #f2c200;
  font-size: 16px;
  color: #2b2d5c;
  font-weight: 600;
  border-radius: 24px;
  border: 2px solid transparent;
  transition: .6s; }

.header__button:hover {
  background: #2b2d5c;
  color: #fff;
  border-color: #fff; }

.header__social {
  position: absolute;
  bottom: 35px;
  right: 100px;
  width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__social i {
  font-size: 24px;
  color: #fff; }

.about {
