.portfolio {
  padding: 100px 0;
  display: flex;
  justify-content: space-between; }

.portfolio__content {
  max-width: 535px;
  background: #f2c200;
  padding: 200px 70px 270px; }

.portfolio__title {
  font-family: Trirong;
  font-size: 40px;
  font-weight: 400;
  color: #2b2d5c;
  margin-bottom: 55px; }

.portfolio__text {
  color: #2b2d5c;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  margin-bottom: 35px; }

.portfolio__link {
  color: #2b2d5c;
  font-size: 16px;
  font-weight: 400; }

.portfolio__images {
  max-width: 1070px;
  display: flex;
  justify-content: space-between; }

.portfolio_pic1 {
  display: flex;
  flex-direction: column;
  margin-right: 31px; }

.port_img1:first-child {
  margin-bottom: 33px; }

.portfolio_pic {
  display: flex;
  flex-wrap: wrap;
  align-content: start;
  justify-content: space-between; }

.port_img {
  margin-bottom: 31px; }

.port_img1,
.port_img {
