.port__button {
  margin-top: 55px;
  margin-left: 47px;
  padding: 15px 65px;
  border: 1px solid #ffcc00;
  border-radius: 32.5px;
  text-align: center;
  text-transform: uppercase;
  color: #f2c200;
  font-size: 16px;
  font-weight: 400;
  transition: all .6s ease;
  display: block; }

.port__button2 {
  display: none; }

.port__button:hover {
  color: #fff;
  background: #2b2d5c;
  border-color: #2b2d5c; }

.blog {
  background-color: rgba(247, 247, 252, 0.6);
  padding: 70px 0 95px; }

.blog__container {
  max-width: 1470px;
  margin: 0 auto; }

.blog__items {
  display: flex;
  justify-content: space-between; }

.blog__card {
  width: 450px;
  height: 615px;
  box-shadow: 0 0 80px 2px rgba(43, 46, 102, 0.2); }

.blog__content {
  padding: 25px 35px; }

.blog__title {
  font-size: 20px;
  color: #2b2d5c;
  line-height: 30px;
  font-weight: 600;
  margin-bottom: 15px; }

.blog__date {
  font-size: 15px;
  color: #2b2d5c;
  font-weight: 500;
  margin-bottom: 15px; }

.blog__date span {
  font-weight: 700; }

.blog__text {
  font-size: 16px;
  color: #595666;
  line-height: 28px;
  text-align: justify;
  margin-bottom: 15px; }

.blog__box {
  display: flex;
  flex-direction: column;
  align-items: center; }

.blog__name {
  font-size: 40px;
  color: #2b2d5c;
  font-family: Trirong;
  font-weight: 500;
  margin-bottom: 40px; }

.blog__subtitle {
  width: 471px;
  font-size: 20px;
  color: #2b2d5c;
  line-height: 38px;
  margin-bottom: 80px;
  text-align: center; }

.work {
  background: url("../images/work__bg.jpg") no-repeat center center/cover;
  background-color: #2f315f;
  padding: 60px 0 75px; }

.work__content {
  max-width: 500px;
  margin: 0 auto;
  text-align: center; }

.work__content input.name,
.work__content input.email,
.work__content textarea {
  width: 100%;
  padding: 15px 30px;
  background: rgba(255, 255, 255, 0.65);
  outline: none;
  border: none;
  margin-bottom: 25px; }

.work__content input.name::placeholder,
.work__content input.email::placeholder,
.work__content textarea::placeholder {
  font-size: 15px;
  color: #2b2d5c; }

.work__content textarea {
  min-height: 90px;
  margin-bottom: 30px; }

.work__content .work__button {
  display: inline-block;
  font-size: 15px;
  color: #2b2d5c;
  text-transform: uppercase;
  font-weight: 500; }

.work__content .work__button:hover {
  color: #fff; }

.work__title {
  font-size: 40px;
  color: #fff;
  margin-bottom: 45px;
  font-family: Trirong; }

.footer {
  background-color: #2b2d5c;
  padding: 90px 0 0; }

.footer__items {
  display: flex;
  justify-content: space-between; }

.footer__logo {
  margin-bottom: 25px; }

.contact h2 {
  font-size: 20px;
  margin-bottom: 10px; }

.contact p {
  font-size: 14px;
  line-height: 22px; }

.contact p:last-child {
  margin-bottom: 35px; }

.social h2,
.social a {
  font-size: 20px;
  color: #fff; }

.social a:not(:last-child) {
  margin-right: 5px; }

.footer__blogs {
  width: 370px; }

.footer__blog-info {
  display: flex;
  align-items: center; }

.footer__blog-title {
  font-size: 24px;
  margin-bottom: 25px;
  font-weight: 300; }

.footer__blog-info {
  margin-bottom: 25px; }

.footer__blog-info img {
  margin-right: 30px; }

.footer__blog-text {
  font-size: 16px;
  line-height: 20px;
  opacity: 0.7;
  font-weight: 300; }

.footer__blog-date {
  margin-top: 10px; }

.footer__blog-date span {
  font-weight: 700;
  color: white; }

.blog__link {
  display: block;
  margin-top: 20px;
  font-size: 16px;
  color: #fff; }

.footer__menu a {
  color: #fff; }

.footer__menu li:not(:last-child) {
  margin-bottom: 12px; }

.insta__photo {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px; }

.insta__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.insta__photo1 {
  display: flex;
  justify-content: space-between; }

.copyright {
  font-size: 14px;
  color: #fff;
  padding: 20px 0; }

@media (max-width: 1599.98px) {
  .portfolio__content {
    max-width: 100%;
    padding: 100px 15px;
    margin-bottom: 31px; }
  .portfolio {
    flex-wrap: wrap; }
  .port_img1 .picture2 {
    margin-bottom: 31px; }
  .portfolio_pic1 {
    margin-left: 31px; }
  .portfolio__images {
    width: 100%;
    justify-content: center;
    margin: 0 auto; }
  .blog__card,
  .blog__card img {
    max-width: 350px; }
  .blog__items {
    justify-content: space-around; }
  .portfolio {
    padding: 0 0 30px; }
  .port__button {
    margin: 0 auto; } }

@media (max-width: 1262.99px) {
  .blog__card,
  .blog__card img {
    max-width: 300px; }
  .blog__name {
    font-size: 30px;
    margin-bottom: 25px; }
  .blog__subtitle {
    width: 320px;
    margin-bottom: 50px; } }

@media (max-width: 1199.98px) {
