    padding-left: 25px;
    padding-right: 25px; }
  .container__about {
    max-width: 1100px;
    margin: 0 auto; }
  .about__items {
    flex-direction: column; }
  .about__picture1 {
    width: 400px;
    height: 300px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center; }
  .clients__items {
    justify-content: space-around; }
  .portfolio__images {
    flex-wrap: wrap; }
  .portfolio_pic1 {
    margin-right: 31px;
    align-items: center; }
  .port_img .last-picture,
  .port_img .picture3,
  .port_img .picture6,
  .port_img1 .picture1,
  .port_img1 .picture2 {
    width: 300px; }
  .portfolio_pic {
    flex-direction: column;
    align-items: center; }
  .port_img1 {
    margin-left: 31px; }
  .footer__container {
    max-width: 1000px;
    margin: 0 auto; }
  .footer {
    padding: 30px; } }

@media (max-width: 991.98px) {
