    align-items: flex-start;
    padding: 20px; }
  .toggle {
    display: flex; }
  .nav__bar ul {
    display: none; }
  .about {
    padding-top: 50px;
    padding-bottom: 50px; }
  .about__content {
    padding-top: 25px;
    padding-bottom: 25px; }
  .about__title {
    font-size: 30px;
    margin-bottom: 5px; }
  .about__subtitle,
  .about__what {
    margin-bottom: 5px; }
  .about__images p {
    margin-bottom: 5px; }
  .blog {
    padding: 25px 0 30px; }
  .blog__box {
    order: -1; }
  .blog__card {
    height: auto; }
  .blog__items {
    flex-direction: column;
    align-items: center; }
  .blog__subtitle {
    width: 750px; }
  .blog__card,
  .blog__card img {
    max-width: 850px; }
  .blog__card:not(:last-child),
  .blog__box {
    margin-bottom: 40px; }
  .footer__items {
    flex-wrap: wrap;
    justify-content: center; }
  .footer__box {
    margin-right: 50px; }
  .footer__container {
    max-width: 800px;
    margin: 0 auto; }
  .footer {
    padding: 20px; } }

@media (max-width: 767.98px) {
  .nav__items .logo {
    margin-bottom: 30px; }
  .about {
    padding: 25px 0; }
  .clients__items {
    flex-wrap: wrap;
    justify-content: center; }
  .clients__logo {
    margin-right: 20px; }
  .portfolio__content {
    padding-top: 25px;
    padding-bottom: 25px; }
  .portfolio__title {
    margin-bottom: 15px; }
  .portfolio__text {
    margin-bottom: 15px; }
  .portfolio_pic1 {
    margin-right: 31px;
    margin-left: 0; }
  .port_img1 {
    margin-left: 15px; }
  .blog__subtitle {
    width: 550px; }
  .blog__card,
  .blog__card img {
    max-width: 850px; }
  .blog__card:not(:last-child),
  .blog__box {
    margin-bottom: 40px; }
  .port__button {
    display: none; }
  .port__button2 {
    display: block; }
  .blog__name {
