  .about__picture1 {
    width: 300px;
    height: 250px;
    margin: 0 auto; }
  .clients {
    padding: 20px 0; }
  .clients__logo:not(:last-child) {
    margin-bottom: 20px; }
  .clients__title {
    margin-bottom: 20px; }
  .portfolio_pic1 {
    margin-right: 0; }
  .port_img1 {
